import React, { Component } from 'react';
import OutputCard from '../components/OutputCard';
import { isMobile } from 'react-device-detect';
import CopyToClipboard from 'react-copy-to-clipboard';
import { ic_chevron_left } from 'react-icons-kit/md/ic_chevron_left';
import { ic_chevron_right } from 'react-icons-kit/md/ic_chevron_right';
import { Icon } from 'react-icons-kit';
import Emoji from '../components/Emoji';

class Page4 extends Component {
  constructor(props) {
    super(props);
    this.state = { current: 1 };
  }

  componentDidMount() {}

  previous() {
    if (this.state.current > 1) {
      this.setState({ current: this.state.current - 1 });
    }
    console.log('previous', this.state.current);
  }

  next() {
    if (this.state.current < this.props.output.length) {
      this.setState({ current: this.state.current + 1 });
    }
    console.log('next', this.state.current);
  }

  render() {
    const { profile, output, nextPage, goBack } = this.props;
    console.log(output);
    return (
      <div className="page4">
        <div className="header-container">
          <div className="back-container">{isMobile && <h4 onClick={() => goBack()}>Back</h4>}</div>
          <div className="button-container">
            <button className="button" text="Retake" onClick={() => nextPage()}>
              Retake
            </button>
          </div>
        </div>
        <div className="page4-padded">
          Emotion : <Emoji name={output[this.state.current - 1].emotion.emotion.emotion} />(
          {output[this.state.current - 1].emotion.emotion.emotion})
        </div>
        {!isMobile && (
          <div className="output-container">
            <div className="output-card-container" style={{ padding: '0 6em' }}>
              <h5>Result</h5>
              <OutputCard>
                <br />
                {output[this.state.current - 1].nepali}
              </OutputCard>
              <CopyToClipboard text={output[this.state.current - 1].nepali}>
                <div
                  style={{ color: '#00a2ff', cursor: 'pointer', textAlign: 'center' }}
                  onClick={() => console.log('a')}
                >
                  <h4>Copy to Clipboard</h4>
                </div>
              </CopyToClipboard>
            </div>
            <div className="output-card-container" style={{ padding: '0 6em' }}>
              <h5>Output</h5>
              <OutputCard>
                <br />
                {output[this.state.current - 1].english}
              </OutputCard>
              <CopyToClipboard text={output[this.state.current - 1].english}>
                <div
                  style={{ color: '#00a2ff', cursor: 'pointer', textAlign: 'center' }}
                  onClick={() => console.log('a')}
                >
                  <h4>Copy to Clipboard</h4>
                </div>
              </CopyToClipboard>
            </div>
          </div>
        )}
        {isMobile && (
          <div className="output-mobile-container">
            <div className="output-card1-container">
              <p>Result</p>
              <OutputCard>
                <br />
                {output[this.state.current - 1].nepali}
              </OutputCard>
            </div>
            <div className="output-card2-container">
              <p>Output</p>
              <OutputCard>
                <br />
                {output[this.state.current - 1].english}
              </OutputCard>
            </div>
          </div>
        )}
        {output.length > 1 && (
          <div className="pagination">
            <Icon
              icon={ic_chevron_left}
              size={32}
              style={{ opacity: this.state.current === 1 ? 0.4 : 1, cursor: 'pointer' }}
              onClick={() => this.previous()}
            />
            Page {this.state.current} of {output.length}
            <Icon
              icon={ic_chevron_right}
              size={32}
              style={{ opacity: this.state.current === output.length ? 0.4 : 1, cursor: 'pointer' }}
              onClick={() => this.next()}
            />
          </div>
        )}
      </div>
    );
  }
}
export default Page4;



