import React, { Component } from 'react';

export default class Emoji extends Component {
  getEmo(name) {
    switch (name) {
      case 'Happy':
        return <img src="/happy.svg" style={{ width: '60px', height: '60px' }} alt="name" />;
      case 'Angry':
        return <img src="/angry.svg" style={{ width: '60px', height: '60px' }} alt="name" />;
      case 'Excited':
        return <img src="/excited.svg" style={{ width: '60px', height: '60px' }} alt="name" />;
      case 'Sad':
        return <img src="/sad.svg" style={{ width: '60px', height: '60px' }} alt="name" />;
      case 'Sarcasm':
        return <img src="/sarcasm.svg" style={{ width: '60px', height: '60px' }} alt="name" />;
      case 'Fear':
        return <img src="/fear.svg" style={{ width: '60px', height: '60px' }} alt="name" />;
      case 'Bored':
        return <img src="/bored.svg" style={{ width: '60px', height: '60px' }} alt="name" />;
      default:
        return '';
    }
  }
  render() {
    const { name } = this.props;
    return <span>{this.getEmo(name)}</span>;
  }
}



